*, *:before, *:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

input {
  border: none;
  outline-color: #F59E0B;
}

.aling-right {
  display: flex;
  justify-content: flex-end;
  margin: 0;
  padding: 0;
}

.error {
  color: rgb(169, 0, 0);
  font-size: 1.5rem;
  font-weight: bold;
  margin: 0;
}

.exito {
  color: rgb(0, 120, 0);
  font-size: 1.5rem;
  font-weight: bold;
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

textarea {
  border: none;
  resize: none;
  outline-color: #F59E0B;
  padding: 1rem;
}

button {
  border: none;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: bold;
  color: #000;
  font-size: 3rem;
  text-align: center;
}

.descripcion-pagina {
  text-align: center;
}

/** Media Queries **/
.form {
  margin-top: 5rem;
}
.form .field {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.form .field label {
  flex: 0 0 10rem;
  font-weight: bold;
}
.form .field input {
  flex: 1;
  padding: 1rem;
  background-color: #e6e8ea;
}

.actions {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .actions {
    display: flex;
    justify-content: space-between;
  }
}
.actions a {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
  color: #000;
  transition: color 0.3s, font-weight 0.3s;
}
.actions a:last-of-type {
  margin-bottom: 0;
}
.actions a:hover {
  font-weight: bold;
}

.alert {
  width: 100%;
  height: 3rem;
  border-radius: 0.2rem;
  margin: 3rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.alert p {
  text-align: center;
  font-weight: bold;
  line-height: 1;
  font-size: 1.8rem;
}
.alert.error {
  border-left: 0.5rem solid rgb(169, 0, 0);
  background-color: #ffa9a9;
}
.alert.error p {
  color: rgb(169, 0, 0);
}
.alert.exito {
  border-left: 0.5rem solid rgb(0, 120, 0);
  background-color: #78ff78;
}
.alert.exito p {
  color: rgb(0, 120, 0);
}

.login {
  margin-top: 5rem;
}
.login .uptask {
  background: linear-gradient(to right, #0891B2 0%, #5bd9f8 100%);
  color: transparent;
  background-clip: text;
  font-size: 7rem;
  text-align: center;
}
@media (min-width: 768px) {
  .login .uptask {
    font-size: 10rem;
  }
}
.login input[type=submit] {
  background-color: #0891B2;
  padding: 1.2rem 3rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 2rem 0;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: #066981;
  cursor: pointer;
}
.login .actions a {
  color: #0891B2;
}
.login .actions a:hover {
  color: #066981;
}

