  .dashboard {
    display: flex;
  }
}
.dashboard .principal {
  flex: 1;
  overflow-y: auto;
}
.dashboard .contenido {
  padding: 2rem;
}
.dashboard .contenido .nombre-pagina {
  font-size: 2.8rem;
  text-align: center;
}
.dashboard .form {
  margin: 2rem auto;
  width: 80%;
}
.dashboard .form label {
  color: #353c46;
}
.dashboard .form input:not([type=submit]) {
  background-color: #FFFFFF;
  width: auto;
}
