.sidebar {
  opacity: 0;
  transition: opacity 0.5s;
  height: 0;
  overflow: hidden;
}
.sidebar.mostrar {
  opacity: 1;
  padding: 2rem;
  height: 100vh;
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(1rem);
  z-index: 2;
}
.sidebar.ocultar {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    opacity: 1;
    height: 100%;
    padding: 2rem;
    background-color: #4338CA;
  }
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .contenedor-sidebar h2 {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
.sidebar .contenedor-sidebar .cerrar-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 2rem;
  width: 2rem;
}
.sidebar .contenedor-sidebar .cerrar-menu:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar .cerrar-menu {
    display: none;
  }
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  padding: 1rem;
  font-size: 2rem;
  margin-bottom: 1.5rem;
  transition: background-color 300ms;
  text-align: center;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    font-size: 1.6rem;
    padding: 0.8rem;
    text-align: start;
  }
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  background-color: #28217b;
  border-radius: 0.5rem;
}

.barra-mobile {
