.barra {
  display: flex;
  background-color: #FFFFFF;
  padding: 1rem 5rem;
  box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.4);
  justify-content: space-between;
  align-items: center;
}
.barra p {
  margin: 0;
  font-size: 1.8rem;
}
.barra p span {
  font-weight: bold;
  color: #4338CA;
}

.cerrar-sesion {
  color: #4338CA;
  font-weight: bold;
  position: relative;
  padding: 0.1rem 0;
  transition: boder 0.2s, color 0.2s;
}
.cerrar-sesion::before {
  content: "";
  position: absolute;
  top: 100%;
  width: 100%;
  height: 3px;
  background-color: #6960d5;
  transform: scaleX(0);
  transition: transform 0.2s;
}
.cerrar-sesion:hover::before {
  transform: scaleX(1);
}
.cerrar-sesion:hover {
  color: black;
}

.no-proyectos {
  margin: 0;
}
.no-proyectos a {
  color: #4338CA;
}

.listado-proyectos {
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .listado-proyectos {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .listado-proyectos {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.listado-proyectos .proyecto {
  display: flex;
  align-items: center;
  padding: 2rem 3rem;
  background-color: #FFFFFF;
  box-shadow: 0px 3px 10px 2px rgba(67, 56, 202, 0.5);
  margin-bottom: 3rem;
  min-height: 12rem;
  line-height: 2;
  border-radius: 1rem;
  transition: transform 0.2s, box-shadow 0.2s;
}
@media (min-width: 768px) {
  .listado-proyectos .proyecto {
    margin-bottom: 0;
  }
}
.listado-proyectos .proyecto:hover {
  transform: scale(1.03);
  box-shadow: 0px 3px 10px 2px rgba(67, 56, 202, 0.75);
}
.listado-proyectos .proyecto:hover a {
  color: #352ca3;
}
.listado-proyectos .proyecto a {
  color: #4338CA;
  font-weight: bold;
  font-size: 2rem;
  transition: color 0.2s;
}

.add-task {
  width: 5rem;
  height: 5rem;
  background-color: #2563EB;
  color: #FFFFFF;
  border-radius: 50%;
  font-size: 3rem;
  font-weight: bold;
  transition: background-color 0.2s;
  position: fixed;
  bottom: 4rem;
  right: 2rem;
}
@media (min-width: 1024px) {
  .add-task {
    bottom: 5rem;
    right: 10rem;
  }
}
.add-task:hover {
  background-color: #1043b3;
  cursor: pointer;
}

.task-list {
  margin: 3.5rem 0;
}
.task-list .no-tasks {
  text-align: center;
}
.task-list p {
  margin: 0.5rem;
}
.task-list summary {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.task {
  background-color: #FFFFFF;
  width: 100%;
  height: auto;
  padding: 2rem;
  padding: 0.5rem 2rem;
  border-radius: 1rem;
  margin-bottom: 1rem;
  box-shadow: 0px 3px 10px 2px rgba(0, 0, 0, 0.25);
}
.task p:nth-child(2) {
  border-top: 1px solid #bdc0c7;
  padding: 1rem 0 0.5rem 0;
}
.task .options {
  display: flex;
  gap: 1rem;
}
.task .options button {
  border: none;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 0.9rem;
  text-transform: uppercase;
  cursor: pointer;
}
.task .options .completa {
  background-color: #4338CA;
  color: #FFFFFF;
}
.task .options .pendiente {
  background-color: #F59E0B;
  color: #FFFFFF;
}
.task .options .delete-task {
  background-color: rgb(169, 0, 0);
  color: #FFFFFF;
}

.modal {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 0 2rem;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  backdrop-filter: blur(0.3rem);
}
.modal .form {
  max-width: 60rem;
  background-color: #FFFFFF;
  padding: 2rem;
  margin: 0 auto;
  border-radius: 0.5rem;
  transform: translateY(-30rem);
  opacity: 0;
  transition: transform 0.3s, opacity 0.4s;
}
.modal .form.animar {
  opacity: 1;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
.modal .form.close {
  transform: translateY(-300%);
  opacity: 0;
}
.modal .form legend {
  margin-bottom: 2rem;
  font-size: 2rem;
  font-weight: bold;
  color: #4338CA;
}
.modal .form .options {
  display: flex;
  flex-direction: column-reverse;
}
@media (min-width: 768px) {
  .modal .form .options {
    flex-direction: row;
    column-gap: 2rem;
    justify-content: flex-end;
  }
}
.modal .form .options .submit-new-task {
  background-color: #4338CA;
  padding: 1.2rem 3rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 2rem 0;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .modal .form .options .submit-new-task {
    width: auto;
  }
}
.modal .form .options .submit-new-task:hover {
  background-color: #352ca3;
  cursor: pointer;
}
@media (min-width: 768px) {
  .modal .form .options .submit-new-task {
    margin: 0;
  }
}
.modal .form .options .close-modal {
  background-color: #F59E0B;
  padding: 1.2rem 3rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 2rem 0;
  border-radius: 0.5rem;
  margin: 0;
}
@media (min-width: 768px) {
  .modal .form .options .close-modal {
    width: auto;
  }
}
.modal .form .options .close-modal:hover {
  background-color: #c57f08;
  cursor: pointer;
}
.modal .form .alert {
  padding: 1px 0.5rem;
}
.modal .form input:not([type=submit]), .modal .form textarea {
  flex: 1;
  padding: 1rem;
  background-color: #e6e8ea;
}

.filters {
  background-color: #FFFFFF;
  border-radius: 0.5rem;
  padding: 1rem 2rem;
}
@media (min-width: 768px) {
  .filters .input-filters {
    display: flex;
    align-items: center;
    gap: 3rem;
  }
}
.filters h2 {
  color: #4338CA;
  font-size: 2.8rem;
  margin-bottom: 1.5rem;
}
@media (min-width: 768px) {
  .filters h2 {
    margin: 0;
  }
}
.filters .field {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  margin-bottom: 1.5rem;
}
@media (min-width: 768px) {
  .filters .field {
    margin: 0;
  }
}
.filters .field input[type=radio] {
  accent-color: #4338CA;
  width: 1.6rem;
  height: 1.6rem;
}

@media (prefers-color-scheme: dark) {
  body {
    background-color: #2c2c3b;
  }

  .tagline, .form label, .nombre-pagina {
    color: #FFFFFF;
  }

  .contenedor-sm .actions a, .dashboard .link {
    color: #f3f4f6;
  }
  .contenedor-sm .actions a:hover, .dashboard .link:hover {
    color: #b9bfca;
  }

  .descripcion-pagina {
    color: #c7ccd5;
  }

  .form .field input {
    background-color: #4d4d67;
    color: #f3f4f6;
  }

  .error {
    color: #F59E0B;
  }

  .barra-mobile {
    background-color: #16161e;
  }

  .barra {
    background-color: #1f2329;
  }
  .barra p {
    color: #F59E0B;
  }
  .barra p span {
    color: #FFFFFF;
  }
  .barra .cerrar-sesion {
    color: #F59E0B;
  }
  .barra .cerrar-sesion::before {
    background-color: #F59E0B;
  }
  .barra .cerrar-sesion:hover {
    color: #f3f4f6;
  }

  .dashboard {
    background-color: #2c2c3b;
  }
  .dashboard .form .field input, .dashboard .form .field textarea {
    background-color: #4d4d67;
    color: #f3f4f6;
  }
  .dashboard .form label {
    color: #FFFFFF;
  }
  .dashboard .filters {
    background-color: #1f2329;
    color: #FFFFFF;
  }
  .dashboard .filters h2 {
    color: #F59E0B;
  }
  .dashboard .task-list .task {
    background-color: #1f2329;
  }
  .dashboard .task-list .task p {
    color: #FFFFFF;
  }
  .dashboard .task-list .task .edit-task {
    background-color: #7a879a;
    color: #FFFFFF;
  }
  .dashboard .form.new-task {
    background-color: #2c2c3b;
  }
  .dashboard .form.new-task legend {
    color: #FFFFFF;
  }
}
@media (prefers-color-scheme: dark) and (min-width: 768px) {
  .sidebar {
    background-color: #16161e;
  }
}
@media (prefers-color-scheme: dark) {
  .sidebar .sidebar-nav a.activo, .sidebar .sidebar-nav a:hover {
    background-color: #F59E0B;
  }
}
@media (prefers-color-scheme: dark) {
  .listado-proyectos .proyecto {
    background-color: #7a879a;
    box-shadow: 0px 3px 10px 2px rgba(0, 0, 0, 0.5);
  }
  .listado-proyectos .proyecto:hover {
    box-shadow: 0px 3px 10px 2px rgba(0, 0, 0, 0.75);
  }
  .listado-proyectos .proyecto:hover a {
    color: #4B5563;
  }
  .listado-proyectos .proyecto a {
    color: #FFFFFF;
  }
}